* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.profile_main {
  position: relative;
  z-index: 90;
  margin: auto;
  /*background-color:  rgba(10, 20, 30, 0.4);*/
}
.samples_cont {
  width: 100%;
}
.profile_slider_inner {
  height: 300px;
  font-size: 34px;
  color: white;
  font-weight: bold;
  text-align: center;
  line-height: 300px;
}
.tab_box {
  z-index: 100;
  position: relative;
  width: 100%;
  -webkit-box-shadow: 0px 2px 7px 0px #535453;
          box-shadow: 0px 2px 7px 0px #535453;
  /*border-bottom: solid 2px blue;*/
  background-color: #e0e0e0;
}
.tab_box .tab_wrap {
  position: relative;
  width: 600px;
  height: 90px;
  margin: auto;
  top: 50px;
}
.tab_wrap .tab_cont {
  list-style: none;
  margin: 0;
  padding: 0;
}
.tab_cont .tab {
  display: inline-block;
  height: 40px;
  margin-right: -2px;
  text-align: center;
  line-height: 40px;
  width: 200px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  margin-top: 5px;
}
.tab_cont .tab.active {
  -webkit-box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 8px 20px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 5px;
  background-color: white;
}
.tab .tab_item {
  text-decoration: none;
  color: #9ea9b3;
}
.tab .tab_item.active {
  color: #6aabb7;
}



